import React, { Component } from "react";
import "./App.css";
import axios from "axios";
import Card from "./components/Card";
import Search from "./components/Search";

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "",
      movies: [],
      orgMovies: [],
    };
  }
  componentDidMount() {
    axios.get("https://www.omdbapi.com/?apikey=45f0782a&s=war").then((response) => {
      this.setState({
        title: response.data.Search.Title,
        movies: response.data.Search,
        orgMovies: response.data.Search,
      });
    });
  }
  handleSearch = (value) => {
    console.log(value);
    const filteredValues = this.state.orgMovies.filter((item) =>
      item.Title.toLowerCase().includes(value.toLowerCase())
    );
    console.log(filteredValues);
    this.setState({ movies: filteredValues });
  };

  render() {
    return (
      <div>
        <Search onSearchValue={this.handleSearch} />
        <div id="movies-wrapper">
          {this.state.movies.map((item, index) => (
            <Card key={index} {...item} />
          ))}
        </div>
      </div>
    );
  }
}
